 .sprotty {
  padding: 0px;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.sprotty-hidden {
  display: block;
  position: absolute;
  width: 0px;
  height: 0px;
}

.sprotty-popup {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  position: absolute;
  background: white;
  border-radius: 5px;
  border: 1px solid;
  max-width: 400px;
  min-width: 100px;
}

.sprotty-popup > div {
  margin: 10px;
}

.sprotty-popup-closed {
  display: none;
}
