.container {
  display: grid;
  box-sizing: border-box;
  width: 100vw;
  height: calc(100vh - 105px);
  grid-template-columns: auto minmax(auto, -webkit-min-content);
  grid-template-columns: auto minmax(auto, min-content);
  grid-template-rows: minmax(auto, -webkit-min-content) auto;
  grid-template-rows: minmax(auto, min-content) auto;
  padding: 0;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.diagram-title {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  display: grid;
  grid-template-columns: auto minmax(auto, -webkit-min-content);
  grid-template-columns: auto minmax(auto, min-content);
  grid-template-rows: 1fr;
  border-right: 1px solid hsl(215.3, 16%, 58.4%);
  border-right: 1px solid var(--silver);
  box-shadow: 0 1px 4px 0 rgba(31, 45, 61, 0.15);
  box-shadow: var(--boxShadow-float);
  padding: 8px 16px;
  padding: var(--layoutDimension-s) var(--layoutDimension-m);
}

.diagram-main {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
  border-right: 1px solid hsl(214.3, 18.9%, 29%);
  border-right: 1px solid var(--slate);
}

.title {
