#toggle-palette {
  min-width: 50px;
  font-weight: 700;
  font-weight: var(--fontWeight-bold);
  font-size: 34px;
  font-size: var(--fontSize-xxl);
}

.palette-main {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 3;
  min-width: 300px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: minmax(auto, -webkit-min-content) auto minmax(auto, -webkit-min-content) minmax(150px, -webkit-min-content);
  grid-template-rows: minmax(auto, min-content) auto minmax(auto, min-content) minmax(150px, min-content)
}

.palette-main>h2 {
  font-family: 'Open Sans', Arial, sans-serif;
  font-size: 28px;
  font-size: var(--fontSize-xl);
  line-height: var(--lineHeight-bodyMedium);
  font-weight: 700;
  font-weight: var(--fontWeight-bold);
  color: hsl(0, 0%, 100%);
  color: var(--white);
  background-color: hsl(215.3, 16%, 58.4%);
  background-color: var(--silver);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 16px 0px 16px 32px;
  padding: var(--layoutDimension-m) 0px var(--layoutDimension-m) var(--layoutDimension-l);
  margin: 0;
}

#tools-palette {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(auto-fill, 100px);
  border-left: 1px solid hsl(215.3, 16%, 58.4%);
  border-left: 1px solid var(--silver);
  padding: 32px 32px 32px 32px;
  padding: var(--layoutDimension-l) var(--layoutDimension-l) var(--layoutDimension-l) var(--layoutDimension-l);
}

.tool {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: 'Open Sans', Arial, sans-serif;
  font-size: 20px;
  font-size: var(--fontSize-l);
  font-weight: 600;
  font-weight: var(--fontWeight-semiBold);
  color: hsl(214.3, 18.9%, 29%);
  color: var(--slate);
}

.tool>img {
  width: 50px;
}

.tool:hover {
  color: hsl(215.3, 16%, 58.4%);
  color: var(--silver);
}

.tool:active {
  color: hsl(0, 0%, 0%);
  color: var(--black);
}

#layers-palette {
  border-left: 1px solid hsl(215.3, 16%, 58.4%);
  border-left: 1px solid var(--silver);
  padding: 32px 32px 32px 32px;
  padding: var(--layoutDimension-l) var(--layoutDimension-l) var(--layoutDimension-l) var(--layoutDimension-l);
}

.layer {
  display: grid;
  grid-template-columns: auto minmax(auto, -webkit-min-content);
  grid-template-columns: auto minmax(auto, min-content);
  grid-template-rows: 1fr;
  -webkit-align-items: center;
          align-items: center;
  font-family: 'Open Sans', Arial, sans-serif;
  font-size: 20px;
  font-size: var(--fontSize-l);
  font-weight: 600;
  font-weight: var(--fontWeight-semiBold);
  color: hsl(214.3, 18.9%, 29%);
  color: var(--slate);
}

input[type="checkbox"] {
  width: 2.2em;
  height: 2.2em;
}

.closed {
  display: none;
}
