.component {
    fill: #ECF0F1;
    stroke: #3498DB;
    stroke-width: 1;
    font-weight: bold;
    border-radius: 15px;

    /* properties for vbox */
    line-height: 10px;
    padding: 10px;
    text-align: center;
}
.component.mouseover {
    stroke: #2980B9;
    stroke-width: 2;
}
.component.selected {
    stroke: #2980B9;
    stroke-width: 4;
}
.component > .heading {
    stroke: #2980B9;
    fill: #2980B9;
    font-size: 11pt;
    stroke-width: 0;
    font-weight: bold;
    text-align: center;
}

.service {
    fill: #FCFFF5;
    stroke: #45BF55;
    stroke-width: 1;
    font-weight: bold;

    /* properties for vbox */
    line-height: 10px;
    padding: 10px;
    text-align: center;
}
.service.mouseover {
    stroke: #167F39;
    stroke-width: 2;
}
.service.selected {
    stroke: #167F39;
    stroke-width: 4;
}
.service > .heading {
    stroke: #167F39;
    fill: #167F39;
    font-size: 9pt;
    stroke-width: 0;
    font-weight: normal;
    text-align: center;
}

.inputport {
    fill: #D96666;
    stroke: #E74C3C;
    stroke-width: 1;
    font-weight: bold;

    /* properties for vbox */
    line-height: 10px;
    padding: 10px;
    text-align: center;
}
.inputport.mouseover {
