}
.outputport.selected {
    stroke: #289976;
    stroke-width: 4;
}

.sprotty-comp {
    line-height: 3px;
    font-weight: normal;
    text-align: left;
}

.listflatcontainer_label {
    font-weight: bold;
}

.sprotty-graph {
    font-size: 14px;
}

.sprotty-label {
    stroke-width: 0;
    fill: #000;
    font-weight: inherit;
    text-align: inherit;
